.sideBar
    color: white
    z-index: 10
    border-radius: 0 0 0.5vh 0.5vh
    background-color: #263C5E
    width: 30vh
    height: 30vh
    display: flex
    flex-direction: column
    gap: 2.5vh
    align-items: flex-start
    position: absolute
    top: 6vh
    right: 0
    h2
        user-select: none
        border-radius: 0.5vh
        margin-left: 1.5vh
        padding: 0.5vh
        text-align: left
        width: 25vh

.showTutorialButton
    font-size: 2.4vh
    margin-top: 1vh
    cursor: pointer
    transition: color 0.2s

.showTutorialButton:hover
    color: #00d18b
    transition: color 0.2s

.contactInfoButton
    font-size: 2.4vh
    cursor: pointer
    transition: color 0.2s

.contactInfoButton:hover
    color: #00d18b
    transition: color 0.2s

.darkModeButton
    font-size: 2.4vh
    position: absolute
    top: 20vh
    color: #b4b4b4
    cursor: default

.contactInfo
    position: relative
    top: -3vh
    left: 4vh
    text-align: left
    font-size: 1.6vh
    white-space: normal
    padding: 0
    margin: 0

.copyrightMessage
    position: absolute
    top: 26vh
    font-size: 1.5vh
