  cursor: pointer;
}
.convolutionalLayer .convolutionalLayerBackground {
  opacity: 0.5;
  transition: fill 0.2s, opacity 0.2s;
}

.convolutionalLayer:hover .convolutionalLayerBackground {
  fill: #b0b0b0;
  opacity: 0.5;
  transition: fill 0.2s, opacity 0.2s;
}

