.modelViewPort
    overflow: hidden
    height: calc(100vh - 13vh)
    padding: 0
    margin: 0
    position: relative
    z-index: 0
    .svgCanvas
        position: absolute
        left: 0

.modelViewPortNull
    overflow: hidden
    height: calc(100vh - 13vh)
    display: flex
    align-content: center
    justify-content: center
    h1
        position: relative
        top: -5vh
        color: gray
        font-size: 2.5vh

.displayedPrediction
    position: absolute
    width: 35vh
    top: 43.5vh
    transform: translateY(-50%)
    right: 0.6vh
    animation: slideIn 0.2s
    h3
        width: 100%
        word-wrap: break-word
